#claim {
  position: relative;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  border: none;
  font-size: 14px;
  align-items: center;
  text-align: center;
  letter-spacing: -0.3px;
  text-align: left;
  margin-bottom: 30px;

  .header {
    width: 100%;
    margin: 0 auto 10px;
    font-weight: 600;
    font-size: 18px;
    line-height: 16px;
    display: flex;
    justify-content: center;
  }

  section {
    width: 100%;
    background: #fff;
    padding: 30px 30px 20px;
    border-radius: 15px;
    box-shadow: 0px 20px 40px rgb(148 148 148 / 6%);
    color: #333;
    margin-bottom: 30px;

    .ant-select {
      width: 100%;
      margin-top: 20px;

      .ant-select-selector {
        background: #fff;
        border: 1px solid #E1E1E1;
        box-sizing: border-box;
        border-radius: 2px;
        width: 100%;

        .ant-select-selection-placeholder {
          font-size: 12px;
          font-weight: normal;
          line-height: 40px;
          color: #999;
        }

        .ant-select-selection-item {
          font-weight: normal;
        }
      }
    }

    .mt-30 {
      margin-top: 30px;
    }

    .mt-20 {
      margin-top: 20px;
    }

    .mt-10 {
      margin-top: 10px;
    }

    .mt-5 {
      margin-top: 5px;
    }

    p {
      font-size: 13px;
      line-height: 24px;
      display: flex;
      align-items: center;
      text-align: center;
      letter-spacing: -0.3px;
      color: #333;
      margin-bottom: 3px;

      &.c-666 {
        color: #666;
      }
    }

    input {
      width: 100%;
      height: 40px;
      line-height: 40px;
      padding: 3px;
      background: #fff;
      color: #333;
      margin-bottom: 6px;
      font-size: 12px;

      border: 1px solid #E1E1E1;
      box-sizing: border-box;
      border-radius: 2px !important;
      padding: 0 12px;

      &::placeholder {
        color: #999;
      }
    }

    .claim-view-btn {
      display: block;
      width: 268px;
      margin: 20px auto 0;
      border-radius: 10px !important;
      height: 40px;
      
      font-weight: 500;
      font-size: 14px;
      line-height: 12px;

      &:disabled {
        background: #ccc !important;
        color: #fff;
      }
    }
  }

  .table {
    width: 100%;
    background: #fff;
    padding: 20px 0px 40px;
    border-radius: 15px;
    // box-shadow: 0px 20px 40px rgb(148 148 148 / 6%);
    // padding: 0 5px 5px;
    border-collapse: collapse;
    width: 100%;
    color: #333;
    margin-bottom: 0;

    legend {
      line-height: 44px;
    }
  }

  table {
    width: 100%;
    margin-top: 20px;

    thead {

      tr {
        border: none;
      }

      th {
        padding: 12px 5px;
        line-height: inherit;
        font-size: 12px;
        line-height: 17px;
        border: none;
        border-bottom: 1px solid rgba($color: #E5E5E5, $alpha: 0.5);
        color: #999;
        padding-left: 40px;

        &:last-child {
          text-align: right;
          padding-left: 0;
          padding-right: 40px;
        }
      }
    }

    td,
    th {
      border: none;
      padding-left: 5px;
      line-height: 32px;
      font-size: 14px;
    }

    tbody {
      tr {
        border: none;

        &:nth-child(2) td {
          padding-top: 6px;
        }

        td {
          padding-left: 40px;



          &:last-child {
            text-align: right;
            padding-left: 0;
            padding-right: 40px;
          }
        }
      }
    }

    span {
      margin-left: 3px;
    }
  }

  .about {
    width: 76%;
    background: #fff;
    padding: 30px 20px 30px;
    border-radius: 15px;
    box-shadow: 0px 20px 40px rgb(148 148 148 / 6%);
    color: #333;
    // margin-top: 18px;
    margin-bottom: 30px;
    margin-left: 30px;

    .title {
      font-weight: 600;
      font-size: 18px;
      line-height: 16px;
      /* identical to box height, or 89% */

      display: flex;
      align-items: center;
      justify-content: center;
      color: #333333;
    }

    fieldset {
      margin-top: 20px;
      color: #333;
      display: block;
      height: 300px;
      overflow-y: scroll;

      &::-webkit-scrollbar {
        width: 4px;
      }

      &::-webkit-scrollbar-track {
        background-color: #EAECEF;
        border-radius: 2px;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #5915E1;
        border-radius: 13px;
      }
    }

    ul li {
      margin: 0 20px;
      font-size: 14px;
      line-height: 23px;
      letter-spacing: 0.5px;
      color: #333;

      &:not(:last-child) {
        list-style: square !important;
      }

      &::marker {
        color: #AC8AF0 !important;
      }

      &:not(:nth-child(1)) {
        margin-top: 23px;
      }

      a {
        color: #5915e1;
      }
    }

    ol {
      margin-left: -6px;

      li {
        margin-right: 20px;
        font-size: 14px;
        line-height: 28px;
      }

      a {
        color: #5915e1;
      }
    }
  }

  .ant-pagination {
    margin-top: 30px;
    text-align: center;

    .ant-pagination-item,
    .ant-pagination-prev,
    .ant-pagination-next {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      min-width: unset;
      width: 24px;
      height: 24px;
    }

    .ant-pagination-prev {
      margin-right: 5px;
    }

    .ant-pagination-next {
      margin-left: -5px;
    }

    .ant-pagination-item {
      border: 1px solid #D8D8D8;
      box-sizing: border-box;
      border-radius: 5px;
      margin-right: 10px;

      a {
        font-weight: 500;
        font-size: 10px;
        line-height: 14px;
        color: #999999;
      }

      &.ant-pagination-item-active {
        background: rgba($color: #4850E5, $alpha: 0.1);
        mix-blend-mode: normal;
        border-radius: 5px;
        border: none;

        a {
          color: #5915E1;
        }
      }

      &:hover a {
        color: #5915E1;
      }
    }

    .pn-previous {
      width: 0;
      height: 0;
      border: 4px solid transparent;
      border-right: 6px solid #333;

      &.gray {
        border-right: 6px solid #d8d8d8;
      }
    }

    .pn-next {
      width: 0;
      height: 0;
      border: 4px solid transparent;
      border-left: 6px solid #333;

      &.gray {
        border-left: 6px solid #d8d8d8;
      }
    }
  }
}

.ant-select-dropdown.claim-select-dropdown {

  .rc-virtual-list {
    padding-right: 8px;
  }

  .ant-select-item {
    padding: 0 8px;
    height: 42px;
    line-height: 42px;
    min-height: unset;
    border-radius: 5px;
    // margin-top: 6px;
  }

  .rc-virtual-list-holder {
    // min-height: unset;
    // height: 190px;
    // overflow-y: scroll;

    &::-webkit-scrollbar {
      width: 4px;
      margin-right: 8px;
    }

    &::-webkit-scrollbar-track {
      background-color: #EAECEF;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #5915E1;
      border-radius: 13px;
    }
  }

  .rc-virtual-list-holder-inner {
    padding: 0 8px;
  }

  .ant-select-item-option-content {
    font-size: 12px;
    line-height: 12px;
    color: #999999;
    height: 42px;
    line-height: 42px;
  }

  .ant-select-item-option-active {
    background-color: #5915E1;

    .ant-select-item-option-content {
      color: #fff;
    }
  }
}

@media screen and (max-width: 800px) {
  #claim {
    width: calc(100% - 30px);

    .about {
      width: 100%;
      margin-left: 0;
      padding: 30px 15px;
      position: relative;

      .shadow {
        position: absolute;
        width: calc(100% - 50px);
        height: 23px;
        background: linear-gradient(transparent, #fff);
        bottom: 30px;
        left: 15px;
      }

      fieldset {
        height: 176px;

        &::-webkit-scrollbar {
          width: 8px;
        }

        &::-webkit-scrollbar-track {
          background-color: #EAECEF;
          border-radius: 4px;
        }

        &::-webkit-scrollbar-thumb {
          background-color: #5915E1;
          border-radius: 13px;
        }
      }

      ul li:not(:nth-child(1)) {
        margin-top: 0;
        line-height: 24px;
      }
    }

    section {
      padding: 30px 15px;

      p.c-666 {
        color: #999;
      }

      .mt-30 {
        margin-top: 16px;
        margin-bottom: 8px;
      }

      .mt-20 {
        margin-top: 10px;
        margin-bottom: 8px;
      }

      .claim-view-btn {
        width: 100%;
        border-radius: 4px !important;
        margin-top: 16px;
      }

      .ant-select .ant-select-selector .ant-select-selection-item {
        font-size: 12px;
      }
    }

    .table {
      padding: 20px 0 30px;
      padding-bottom: 30px !important;

      thead th,
      tbody td {
        padding-left: 15px;
      }

      thead th:last-child,
      tbody td:last-child {
        padding-right: 15px;
      }

      tbody tr {
        height: 42px;
        // border-bottom: 1px solid rgba($color: #e6e6e6, $alpha: 0.5);

        &:nth-child(2) td {
          padding-top: 0;
        }
      }
    }
  }

  .ant-select-dropdown.claim-select-dropdown {

    .ant-select-item {
      margin-top: 0;
    }

    .rc-virtual-list-holder-inner {
      padding-right: 12px;

      &::-webkit-scrollbar {
        width: 8px;
      }

      &::-webkit-scrollbar-track {
        background-color: #EAECEF;
        border-radius: 4px;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #5915E1;
        border-radius: 13px;
      }
    }
  }
}